.button {
  text-decoration: none;
  color: $white;
  text-transform: uppercase;
  background-color: $basic-green;
  border: none;
  outline: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-align: center;

  @media (min-width: $tablet) {
    font-size: 20px;
    line-height: 20px;
  }

  &--size {
    font-size: 14px;
    line-height: 18px;

    @media (min-width: $tablet) {
      font-size: 20px;
      line-height: 20px;
    }
  }
}
